/*  <buttons>  */
.btn {
  display: inline-flex;
  height: 32px;
  justify-content: center;
  align-items: center;
  color: #444;
  font-size: 1.1rem;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 15px;
  cursor: pointer;
  transition: all 0.2s;
}
a.btn {
  text-decoration: none;
}
.btn:hover {
  color: #222;
  border-color: #888;
}
.btn:active {
  box-shadow: inset 0 2px 4px rgba(34,34,34,0.4);
}
.btn:disabled,
.btn[disabled] {
  pointer-events: none;
  color: #888;
  background-color: #eee;
  border-color: #eee;
}
.btn--blue {
  color: #fff;
  background-color: #4c95ca;
  border-color: #4c95ca;
}
.btn--blue:hover {
  color: #fff;
  background-color: #005a8a;
  border-color: #005a8a;
}
.btn--blue:active {
  box-shadow: inset 0 4px 8px rgba(34,34,34,0.4);
}
.btn--green {
  color: #fff;
  background-color: #81c81e;
  border-color: #81c81e;
}
.btn--green:hover {
  color: #fff;
  background-color: #56a000;
  border-color: #56a000;
}
.btn--green:active {
  box-shadow: inset 0 4px 8px rgba(34,34,34,0.4);
}
.btn--bordered {
  color: #81c81e;
  border-color: #81c81e;
}
.btn--bordered:hover {
  color: #00a619;
  border-color: #00a619;
}
.btn--middle {
  height: 38px;
  font-size: 1.3rem;
}
.btn--large {
  height: 46px;
  font-size: 1.5rem;
  padding: 0 22px;
}
.btn--fullwidth {
  width: 100%;
}
.btn--dialog::after {
  border: solid transparent;
  border-width: 5px 5px 0;
  border-top-color: #444;
  margin-left: 10px;
  content: '';
}
/*  </buttons>  */
/*  <inputs>  */
.input-text,
.textarea {
  width: 100%;
  height: 38px;
  color: #444;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 15px;
  transition: all 0.2s;
}
.input-text::placeholder,
.textarea::placeholder {
  opacity: 1;
  color: #888;
}
.input-text:hover,
.textarea:hover,
.input-text:focus,
.textarea:focus {
  border-color: #888;
  outline: none;
}
.input-text--large,
.textarea--large {
  height: 46px;
}
.input-text--success,
.textarea--success {
  border-color: #00a619;
}
.input-text--success:hover,
.textarea--success:hover,
.input-text--success:focus,
.textarea--success:focus {
  border-color: #00a619;
}
.input-text--error,
.textarea--error {
  border-color: #c81e2c;
}
.input-text--error:hover,
.textarea--error:hover,
.input-text--error:focus,
.textarea--error:focus {
  border-color: #c81e2c;
}
.input-text--bordered,
.textarea--bordered {
  border-color: #81c81e;
}
.input-text--bordered:hover,
.textarea--bordered:hover,
.input-text--bordered:focus,
.textarea--bordered:focus {
  border-color: #00a619;
}
.input-text--email,
.textarea--email {
  background: #fff url("../i/icons/icon-user-green.svg") no-repeat 14px center/15px auto;
  padding-left: 42px;
}
.input-text--password,
.textarea--password {
  background: #fff url("../i/icons/icon-lock-green.svg") no-repeat 14px center/13px auto;
  padding-left: 42px;
}
.input-text--letter,
.textarea--letter {
  background: #fff url("../i/icons/icon-letter-green.svg") no-repeat 14px center/15px auto;
  padding-left: 42px;
}
.input-text--phone,
.textarea--phone {
  background: #fff url("../i/icons/icon-phone-green.svg") no-repeat 14px center/12px auto;
  padding-left: 42px;
}
.input-text--card,
.textarea--card {
  background: #fff url("../i/icons/icon-card-green.svg") no-repeat 14px center/15px auto;
  padding-left: 42px;
}
.input-text:disabled,
.textarea:disabled {
  color: #888;
  background-color: #eee;
  border-color: #eee;
}
.input-text:disabled:hover,
.textarea:disabled:hover,
.input-text:disabled:focus,
.textarea:disabled:focus {
  background-color: #eee;
  border-color: #eee;
}
.input-text:disabled::placeholder,
.textarea:disabled::placeholder {
  color: #888;
}
.textarea {
  height: 122px;
  min-height: 122px;
  padding: 10px 15px;
  resize: vertical;
  transition: border 0.2s, background-color 0.2s;
}
.textarea--large {
  height: 160px;
  min-height: 160px;
}
.js-input-dynamic-width {
  box-sizing: content-box;
}
.select {
  width: 100%;
}
.checkbox-toggle {
  position: absolute;
  left: -9999px;
}
.checkbox-toggle + label {
  display: inline-flex;
  position: relative;
  width: 42px;
  height: 24px;
  background-color: rgba(44,200,30,0.6);
  border-radius: 12px;
  transition: all 0.2s;
  cursor: pointer;
}
.checkbox-toggle + label::before {
  position: absolute;
  top: 1px;
  bottom: 1px;
  left: 1px;
  width: 22px;
  background-color: #fff;
  border-radius: 50%;
  content: '';
  transition: all 0.2s;
}
.checkbox-toggle:checked + label {
  background-color: #2cc81e;
}
.checkbox-toggle:checked + label::before {
  left: 100%;
  margin-left: -1px;
  transform: translateX(-100%);
}
.input-file {
  position: absolute;
  left: -9999px;
}
.input-file + label {
  display: inline-flex;
}
.toggler {
  display: inline-flex;
  position: relative;
  width: 42px;
  height: 24px;
  background-color: rgba(44,200,30,0.6);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s;
}
.toggler::before {
  position: absolute;
  top: 1px;
  left: 1px;
  bottom: 1px;
  width: 22px;
  background-color: #fff;
  border-radius: 50%;
  content: '';
  transition: all 0.2s;
}
.toggler--active {
  background-color: #2cc81e;
}
.toggler--active::before {
  right: 1px;
  left: auto;
}
/*  </inputs>  */
/*  <labels>  */
.label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  vertical-align: middle;
  height: 22px;
  font-size: 1.3rem;
  white-space: nowrap;
  border-radius: 2px;
  padding: 0 5px;
}
.label--red {
  color: #fff;
  background-color: #c81e2c;
}
.label--red .label__text {
  color: #c81e2c;
  border: solid #c81e2c;
  border-width: 1px 0;
}
.label--gray {
  color: #fff;
  background-color: #888;
}
.label--gray .label__text {
  color: #888;
  border: solid #888;
  border-width: 1px 0;
}
.label__text {
  height: 100%;
  color: #000;
  background-color: #fff;
  padding: 1px 6px;
  margin: 0 -4px 0 5px;
}
/*  </labels>  */
/*  <pagination>  */
.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  padding-top: 56px;
}
.pagination--small {
  padding-top: 0;
}
.pagination--small .btn--large {
  height: 32px;
}
.pagination__loadmore {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.pagination__item {
  min-width: 46px;
  font-weight: 500;
  font-size: 1.3rem;
  padding: 0 10px;
  margin-right: 3px;
}
.pagination__item:last-child {
  margin-right: 0;
}
.pagination--small .pagination__item {
  min-width: 32px;
  padding: 0 4px;
}
.pagination__item--prev::before {
  width: 8px;
  height: 8px;
  border: solid #444;
  border-width: 2px 0 0 2px;
  content: '';
  transform: rotate(-45deg);
}
.pagination__item--prev[disabled]::before {
  border-color: #888;
}
.pagination__item--next::before {
  width: 8px;
  height: 8px;
  border: solid #444;
  border-width: 2px 2px 0 0;
  content: '';
  transform: rotate(45deg);
}
.pagination__item--next[disabled]::before {
  border-color: #888;
}
.pagination__item--current {
  pointer-events: none;
  color: #fff;
  background-color: #81c81e;
  border-color: #81c81e;
}
.pagination__item--space {
  pointer-events: none;
  padding: 0 30px;
}
.pagination--small .pagination__item--space {
  padding: 0 18px;
}
/*  </pagination>  */
/*  <modal>  */
.modal {
  display: none;
  max-width: 750px;
  border-top: 3px solid #81c81e;
  border-radius: 3px;
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}
.modal .fancybox-close-small {
  top: 13px;
  right: 15px;
  width: 35px;
  height: 35px;
}
.modal .fancybox-close-small::after {
  top: 50%;
  left: 50%;
  width: 35px;
  height: 35px;
  color: #444;
  font: 300 3rem/1 Roboto;
  background-color: transparent;
  border-radius: 0;
  transform: translate(-50%, -50%);
  transition: all 0.2s;
}
.modal .fancybox-close-small:hover::after {
  color: #888;
  background-color: ;
}
.modal__title {
  color: #222;
  font-size: 2.3rem;
  border-bottom: 1px solid #f8f8f8;
  padding: 20px 24px;
  margin: 0 -24px;
}
.modal__btns {
  text-align: right;
  border-top: 1px solid #eee;
  padding-top: 15px;
  margin-top: 25px;
}
.modal__btns .btn {
  margin-left: 15px;
}
/*  </modal>  */
/*  <layout>  */
.fadeIn {
  animation: fadeIn 0.5s;
}
.fadeOut {
  animation: fadeOut 0.5s;
}
.form__row {
  position: relative;
  margin: 16px 0;
}
.form__row:last-child {
  margin-bottom: 0;
}
.form__row--big {
  margin: 28px 0;
}
/*  </layout>  */
/*  <text>  */
h1,
h2,
h3 {
  color: #222;
  font-weight: 900;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2.3rem;
}
h3 {
  font-size: 1.7rem;
}
.color-light {
  color: #888;
}
.color-gray {
  color: #444;
}
.color-dark {
  color: #222;
}
.color-red {
  color: #c81e2c;
}
.color-green {
  color: #00a619;
}
/*  <text>  */
/*  <animations>  */
/*  </animations>  */
/*  <dialog>  */
.dialog {
  display: none;
  position: absolute;
  width: 140px;
  background-color: #fff;
  border-top: 3px solid #81c81e;
  border-radius: 3px;
  margin-top: 15px;
  box-shadow: 0 4px 8px rgba(34,34,34,0.4);
}
.dialog::before {
  position: absolute;
  top: -8px;
  left: 12px;
  border: solid transparent;
  border-width: 0 6px 8px;
  border-bottom-color: #81c81e;
  content: '';
}
.dialog--active {
  display: block;
  animation: fadeIn 0.5s;
}
.dialog__item {
  display: block;
  color: #444;
  font-size: 1.3rem;
  text-decoration: none;
  padding: 12px 14px;
  transition: all 0.2s;
}
a:hover.dialog__item {
  color: #222;
  background-color: #eee;
}
/*  </dialog>  */
/*  <tagsinput>  */
.tagsinput {
  width: 100%;
  height: 122px;
  color: #444;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 8px 6px;
  transition: all 0.2s;
}
.tagsinput:hover,
.tagsinput:focus {
  border-color: #888;
  outline: none;
  cursor: text;
}
.tagsinput .tag {
  display: inline-flex;
  align-items: center;
  height: 22px;
  color: #fff;
  font-size: 1.3rem;
  text-transform: uppercase;
  white-space: nowrap;
  background-color: #888;
  border-radius: 2px;
  padding-left: 8px;
  margin: 0 6px 6px 0;
}
.tagsinput .tag a {
  position: relative;
  height: 100%;
  color: #fff;
  font-size: 1.3rem;
  text-decoration: none;
  text-indent: -9999px;
  padding: 0 8px;
}
.tagsinput .tag a::before {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 1.5rem;
  text-indent: 0;
  content: '×';
}
.tagsinput > div {
  display: inline-flex;
}
.tagsinput > div input {
  color: #444;
  background-color: transparent;
  border: 0;
}
/*  </tagsinput>  */
/*  <notification>  */
.notification {
  animation: fadeIn 0.5s;
  position: fixed;
  top: 80px;
  right: 80px;
}
.notification:empty {
  display: none;
}
.notification__item {
  position: relative;
  width: 360px;
  color: #fff;
  font-weight: normal;
  font-size: 1.3rem;
  border-radius: 2px;
  padding: 10px 34px 10px 38px;
  margin-bottom: 7px;
}
.notification__item:last-child {
  margin-bottom: 0;
}
.notification__item--notice {
  background: #70aad5 url("../i/icons/icon-notice.svg") no-repeat 14px center/12px auto;
}
.notification__item--error {
  background: #d34b56 url("../i/icons/icon-error.svg") no-repeat 13px center/14px auto;
}
.notification__item--success {
  background: #33b847 url("../i/icons/icon-success.svg") no-repeat 14px center/12px auto;
}
.notification__item-close {
  position: absolute;
  top: 50%;
  right: 10px;
  width: 16px;
  height: 16px;
  transform: translateY(-50%);
}
.notification__item-close::before,
.notification__item-close::after {
  position: absolute;
  top: 2px;
  left: 50%;
  bottom: 2px;
  width: 1px;
  background-color: #fff;
  margin-left: -1px;
  content: '';
}
.notification__item-close::before {
  transform: rotate(45deg);
}
.notification__item-close::after {
  transform: rotate(-45deg);
}
/*  </notification>  */
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-o-keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-moz-keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
