/*  <fonts>  */
@font-face {
  font-family: logo;
  src: url("../fonts/raleway-500-normal.woff2") format('woff2'), url("../fonts/raleway-500-normal.woff") format('woff');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Black.woff2") format('woff2'), url("../fonts/Roboto-Black.woff") format('woff');
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Light.woff2") format('woff2'), url("../fonts/Roboto-Light.woff") format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Medium.woff2") format('woff2'), url("../fonts/Roboto-Medium.woff") format('woff');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Bold.woff2") format('woff2'), url("../fonts/Roboto-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/Roboto-Regular.woff2") format('woff2'), url("../fonts/Roboto-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
/*  </fonts>  */
/*  <general>  */
html {
  min-height: 100vh;
  font-size: 62.5%;
  cursor: default;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #444;
  font: normal 1.3rem/1.3 Roboto;
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #444;
  font: inherit;
  text-decoration: underline;
  transition: all 0.2s;
}
a:hover {
  text-decoration: none;
}
img {
  vertical-align: top;
  max-width: 100%;
}
.main {
  flex: 1;
  background-color: #f7f7f7;
  padding-bottom: 30px;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  background-color: #fafafa;
  border-bottom: 1px solid #eee;
}
.header .container {
  padding-top: 20px;
}
.header__short {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 30px;
}
.header__place,
.header__bookmarks,
.header__auth {
  position: relative;
  color: #444;
  text-decoration: none;
  white-space: nowrap;
  margin-right: 50px;
}
.header__place svg,
.header__bookmarks svg,
.header__auth svg {
  vertical-align: -4px;
  width: 16px;
  height: 16px;
}
.header__place svg path,
.header__bookmarks svg path,
.header__auth svg path {
  fill: #444;
  transition: all 0.2s;
}
a:hover.header__place svg path,
a:hover.header__bookmarks svg path,
a:hover.header__auth svg path {
  fill: #888;
}
.header__general {
  display: flex;
  align-items: center;
  background-color: #fafafa;
  padding: 0 0 20px;
  transition: all 0.2s;
}
.header__general.is_stuck {
  z-index: 1000;
  padding: 15px 0;
}
.header__general.is_stuck::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -10000%;
  width: 20000%;
  background-color: #fafafa;
  box-shadow: 0 3px 6px rgba(34,34,34,0.2);
  content: '';
  transition: all 0.2s;
}
.header__logo {
  position: relative;
  font-weight: 500;
  font-size: 2.4rem;
  font-family: logo;
  white-space: nowrap;
  text-decoration: none;
}
.header__phone {
  font-weight: 300;
  font-size: 1.7rem;
  text-decoration: none;
}
.header__search {
  flex: 1;
  position: relative;
  margin: 0 56px 0 28px;
  box-shadow: 0 2px 3px rgba(34,34,34,0.1);
  transition: all 0.2s;
}
.header__search--focus {
  box-shadow: 0 2px 8px rgba(34,34,34,0.2);
}
.header__search-input {
  position: relative;
  z-index: 10000;
  width: 100%;
  height: 46px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 50px 0 22px;
}
.header__search-input:focus {
  outline: none;
}
.header__search-btn {
  position: absolute;
  top: 50%;
  right: 16px;
  width: 30px;
  height: 30px;
  background: transparent url("data:image/svg+xml;base64,") no-repeat center center/18px auto;
  border: 0;
  cursor: pointer;
  transform: translateY(-50%);
}
.header__search-result {
  display: none;
  position: absolute;
  z-index: 9999;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 0 0 2px 2px;
  padding: 20px;
  margin-top: -1px;
  box-shadow: 0 2px 8px rgba(34,34,34,0.2);
}
.header__search-result--active {
  display: block;
  animation: fadeIn 0.5s;
}
.header__cart {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 35px;
}
.header__cart::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 21px;
  height: 18px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(-50%);
}
.header__cart-count {
  position: relative;
  color: #fff;
  font-weight: 500;
  line-height: 2.8rem;
  background-color: #4c95ca;
  border-radius: 1.4rem;
  padding: 0 10px;
  margin-right: 30px;
}
.header__cart-count:empty {
  width: 28px;
  height: 28px;
  color: #444;
  background-color: #ddd;
}
.header__cart-count:empty::before {
  content: '0';
}
.header__cart-count::after {
  position: absolute;
  top: 0;
  right: -15px;
  bottom: 0;
  width: 1px;
  background-color: #ddd;
  content: '';
}
.header__cart-summ {
  color: #444;
  font-weight: 500;
  margin: 0 0 6px;
}
.header__cart-shipper {
  color: #888;
  font-size: 1.1rem;
}
.header__cart-link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.header__nav {
  display: flex;
  justify-content: space-between;
}
.header__nav-item {
  position: relative;
  font-weight: 500;
  font-size: 1.2rem;
  text-decoration: none;
  margin-bottom: 20px;
}
.header__nav-item:first-child {
  padding-left: 20px;
}
.header__nav-item:first-child::after {
  position: absolute;
  top: 50%;
  left: 0;
  width: 12px;
  height: 11px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
  transform: translateY(calc(-50% - 2px));
}
.header__nav-item--active::before,
.header__nav-item::before {
  position: absolute;
  bottom: -20px;
  left: 50%;
  width: 0;
  height: 3px;
  background-color: #81c81e;
  content: '';
  transform: translateX(-50%);
  transition: all 0.2s;
}
.header__nav-item--active::before,
a:hover.header__nav-item::before {
  width: 100%;
}
.header-search-result__title {
  color: #222;
  font-weight: 500;
  margin-bottom: 20px;
}
.header-search-result__nav {
  display: flex;
  flex-wrap: wrap;
}
.header-search-result__nav-item {
  white-space: nowrap;
  margin: 0 12px 12px 0;
}
.header-search-result__latest {
  display: flex;
  flex-direction: column;
  margin-top: 16px;
}
.header-search-result__latest-title {
  color: #222;
  font-weight: 500;
  margin-bottom: 8px;
}
.header-search-result__latest-item {
  align-self: flex-start;
  text-decoration: none;
  margin: 12px 0;
}
a:hover.header-search-result__latest-item {
  text-decoration: underline;
}
.header-cabinet {
  flex-shrink: 0;
  height: 60px;
  background-color: #444;
}
.header-cabinet .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
.header-cabinet__logo {
  color: #fff;
  font-weight: 500;
  font-size: 2rem;
  font-family: logo;
  white-space: nowrap;
  text-decoration: none;
}
.header-cabinet__logo span {
  color: #81c81e;
}
.header-cabinet__nav {
  display: flex;
}
.header-cabinet__nav-item {
  display: flex;
  align-items: center;
  height: 60px;
  color: #ddd;
  text-decoration: none;
  margin-left: 20px;
}
.header-cabinet__nav-item:first-child {
  margin-left: 0;
}
.header-cabinet__nav-item::after {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 0;
  background-color: #81c81e;
  content: '';
  transition: all 0.2s;
}
a:hover.header-cabinet__nav-item,
.header-cabinet__nav-item--active {
  position: relative;
  color: #fff;
}
a:hover.header-cabinet__nav-item::after,
.header-cabinet__nav-item--active::after {
  height: 3px;
}
.easy-autocomplete {
  width: 100%;
}
.easy-autocomplete-container {
  position: absolute;
  z-index: 9999;
  width: 100%;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 0 0 2px 2px;
  margin-top: -1px;
  box-shadow: 0 2px 8px rgba(34,34,34,0.2);
}
.easy-autocomplete-container ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.easy-autocomplete-container a {
  display: block;
  color: #222;
  font-weight: 500;
  font-size: 1.3rem;
  text-decoration: none;
  background-color: #fff;
  padding: 10px 22px;
}
.easy-autocomplete-container a:hover {
  background-color: #eee;
}
.easy-autocomplete-container .selected a {
  background-color: #eee;
}
/*  </header>  */
/*  <footer>  */
.footer {
  overflow: hidden;
  color: #ddd;
  background-color: #444;
  padding: 45px 0 0;
}
.footer--short {
  padding: 0;
}
.footer .container {
  display: flex;
  flex-wrap: wrap;
}
.footer__nav {
  flex: 1;
  display: flex;
}
.footer__nav-block {
  display: flex;
  flex-direction: column;
  margin: 10px 70px 0 0;
}
.footer__nav-title {
  font-weight: 500;
  margin: 0 0 5px;
}
.footer__nav-item {
  align-self: flex-start;
  color: #ddd;
  text-decoration: none;
  margin: 6px 0;
}
a:hover.footer__nav-item {
  text-decoration: underline;
}
.footer__general {
  width: 260px;
}
.footer__logo {
  font-weight: 500;
  font-size: 2.4rem;
  font-family: logo;
  white-space: nowrap;
  text-decoration: none;
}
.footer__logo a {
  color: #ddd;
  text-decoration: none;
}
.footer__slogan {
  line-height: 2.1rem;
  margin-bottom: 20px;
}
.footer__phone,
.footer__email {
  position: relative;
  font-weight: 300;
  font-size: 1.7rem;
  padding-left: 30px;
  margin: 14px 0;
}
.footer__phone::before,
.footer__email::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.footer__phone a,
.footer__email a {
  color: #ddd;
  text-decoration: none;
}
.footer__phone::before {
  width: 16px;
  height: 22px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
}
.footer__email::before {
  width: 18px;
  height: 18px;
  background: url("data:image/svg+xml;base64,") no-repeat 0 0/cover;
  content: '';
}
.footer__social {
  display: flex;
  margin-top: 20px;
}
.footer__social-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  border: 1px solid #ddd;
  border-radius: 50%;
  margin-right: 14px;
}
.footer__social-item:last-child {
  margin-right: 0;
}
.footer__social-item svg {
  max-width: 25px;
  max-height: 25px;
  fill: #ddd;
  transition: all 0.2s;
}
a:hover.footer__social-item {
  background-color: #ddd;
}
a:hover.footer__social-item svg {
  fill: #444;
}
.footer__copyright {
  position: relative;
  width: 100%;
  height: 50px;
  line-height: 5rem;
  background-color: #222;
  margin-top: 45px;
}
.footer--short .footer__copyright {
  margin-top: 0;
}
.footer__copyright::before,
.footer__copyright::after {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10000%;
  background-color: #222;
  content: '';
}
.footer__copyright::before {
  left: -10000%;
}
.footer__copyright::after {
  left: 100%;
}
/*  </footer>  */
/*  <breadcrumbs>  */
.breadcrumbs {
  display: flex;
  list-style: none;
  color: #888;
  font-size: 1.1rem;
  padding: 0;
  margin: 18px 0;
}
.breadcrumbs li {
  position: relative;
  margin-right: 20px;
}
.breadcrumbs li::after {
  position: absolute;
  top: 50%;
  right: -14px;
  width: 8px;
  height: 1px;
  background-color: #888;
  content: '';
}
.breadcrumbs li:last-child {
  color: #444;
  margin-right: 0;
}
.breadcrumbs li:last-child::after {
  display: none;
}
.breadcrumbs a {
  color: #888;
  text-decoration: none;
}
.breadcrumbs a:hover {
  text-decoration: underline;
}
/*  </breadcrumbs>  */
/*  <category>  */
.category__name {
  color: #222;
  font-weight: 900;
  font-size: 3rem;
  margin: 0 0 5px;
}
.category__address {
  color: #444;
}
.category__address a {
  text-decoration-style: dashed;
  margin-left: 5px;
}
.category__tags {
  margin: 15px 0 24px;
}
.category__tags-item {
  display: inline-flex;
  align-items: center;
  height: 30px;
  text-decoration: none;
  white-space: nowrap;
  background-color: #ddd;
  border-radius: 15px;
  padding: 0 15px;
  margin-right: 15px;
}
a:hover.category__tags-item {
  color: #fff;
  background-color: #888;
}
.category__wrap {
  display: flex;
  align-items: flex-start;
  margin-top: 15px;
}
.category__sidebar {
  width: 278px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 2px rgba(34,34,34,0.1);
}
.category-filter__result {
  color: #222;
  font-weight: 500;
  text-align: center;
  border-bottom: 1px solid #eee;
  padding: 20px 0;
  margin: 0 20px;
}
.category-filter__result p {
  margin: 3px 0;
}
.category-filter__result .btn {
  margin-top: 12px;
}
.category-filter__range {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.category-filter__range-item,
.category-filter__range-single {
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: calc(50% - 3px);
  height: 38px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0 8px;
}
.category-filter__range-item span,
.category-filter__range-single span {
  flex-shrink: 0;
  color: #888;
}
.category-filter__range-item input,
.category-filter__range-single input {
  width: calc(100% - 45px);
  margin: 0 5px;
}
.category-filter__range-single {
  width: 100%;
}
.category-filter__range-input {
  color: #444;
  background-color: transparent;
  border: 0;
}
.category-filter__range-place {
  width: 100%;
  margin: 20px 0 6px;
}
.category-filter__range-unit {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
}
.category-filter__region-wrap {
  overflow-y: auto;
  max-height: 450px;
  margin-top: 25px;
}
.category-filter__region-section {
  margin-top: 25px;
}
.category-filter__region-section:first-child {
  margin-top: 0;
}
.category-filter__region-section::before {
  display: block;
  color: #888;
  text-transform: uppercase;
  margin-bottom: 13px;
  content: attr(data-section-letter);
}
.category-filter__region-section--hidden {
  display: none;
}
.category-filter__region-section:empty::before {
  display: none;
}
.category-filter__region-item {
  margin-bottom: 16px;
}
.category-filter__region-item span {
  color: #888;
}
.category-filter__region-item--hidden {
  display: none;
}
.category-filter__region-notFound {
  display: none;
  margin: 15px 0;
}
.category-filter__region-notFound--active {
  display: block;
  animation: fadeIn 0.5s;
}
.category-filter__categories {
  display: flex;
  flex-direction: column;
  margin-top: -10px;
}
.category-filter__categories-item {
  align-self: flex-start;
  text-decoration: none;
  margin: 8px 0;
}
a:hover.category-filter__categories-item {
  text-decoration: underline;
}
.category-filter__check {
  display: flex;
  flex-direction: column;
  margin-top: -5px;
}
.category-filter__check-item {
  align-self: flex-start;
  text-decoration: none;
  margin: 8px 0;
}
.category-filter__check-item--hidden {
  display: none;
}
.category-filter__clear {
  padding: 24px 0 26px;
  margin: 0 20px;
}
.category-filter__row {
  width: 100%;
  margin-top: 16px;
}
.category-cats__back {
  line-height: 2.1rem;
  border-bottom: 1px solid #eee;
  padding: 18px 0;
  margin: 0 20px;
}
.category-cats__back a {
  display: block;
  color: #888;
  text-decoration: none;
}
.category-cats__back a:hover {
  text-decoration: underline;
}
.category-cats__list {
  display: flex;
  flex-direction: column;
  margin-top: -8px;
}
.category-cats__list-item {
  line-height: 2.1rem;
  margin: 8px 0;
}
.category-cats__list-item--hidden {
  display: none;
}
.category-cats__list-item a {
  color: #444;
  text-decoration: none;
}
.category-cats__list-item a:hover {
  text-decoration: underline;
}
.category-sidebar__block {
  border-bottom: 1px solid #eee;
  padding: 15px 0 25px;
  margin: 0 20px;
}
.category-sidebar__block:last-child {
  border-bottom: 0;
}
.category-sidebar__block--hidden {
  display: none;
}
.category-sidebar__block--collapsed {
  padding: 15px 0 0;
}
.category-sidebar__block-title {
  position: relative;
  color: #222;
  font-weight: 500;
  font-size: 1.7rem;
  padding-right: 30px;
  margin-bottom: 18px;
  cursor: pointer;
}
.category-sidebar__block-title.js-category-block-title-collapse::before {
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: -20px;
  width: 3px;
  background-color: #888;
  content: '';
}
.category-sidebar__block-title.js-category-block-title-collapse::after {
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  border: solid #888;
  border-width: 2px 2px 0 0;
  content: '';
  transform: rotate(-45deg) translateY(-50%);
  transition: all 0.2s;
}
.category-sidebar__block-title--collapsed.js-category-block-title-collapse::after {
  right: 8px;
  transform-origin: right top;
  transform: rotate(135deg) translateY(-50%);
}
.category-sidebar__showrows {
  display: block;
  position: relative;
  width: 100%;
  color: #888;
  font-weight: 500;
  text-decoration: none;
  margin-top: 8px;
}
.category-sidebar__showrows::after {
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  border: solid #888;
  border-width: 0 0 2px 2px;
  content: '';
  transform-origin: top left;
  transform: rotate(-45deg) translateY(-50%);
  transition: all 0.2s;
}
a:hover.category-sidebar__showrows {
  text-decoration: underline;
}
.category-sidebar__showrows--hidden {
  display: none;
}
.category-sidebar__showrows--collapsed::after {
  transform-origin: 3px -1px;
  transform: rotate(135deg) translateY(-50%);
}
.category__main {
  flex: 1;
  margin-left: 15px;
}
.category__system {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  border-radius: 2px;
  padding: 18px 28px;
  margin-bottom: 14px;
  box-shadow: 0 1px 2px rgba(34,34,34,0.1);
}
.category__sort .select {
  width: 245px;
}
.category__sort-current {
  vertical-align: middle;
  width: 38px;
  padding: 0;
}
.category__sort-current--desc {
  background: url("../i/icons/icon-sort-desc.svg") no-repeat center center/16px auto;
}
.category__sort-current--asc {
  background: url("../i/icons/icon-sort-asc.svg") no-repeat center center/16px auto;
}
.category__view {
  display: flex;
}
.category__view-item {
  margin-right: 14px;
}
.category__view-item:last-of-type {
  margin-right: 0;
